.footerWraper {
  background: var(--gradient-text);
  padding: 93px 15px;
}
.footerBlocks {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footerLabel {
  text-decoration: none;
  color: var(--text-color-white);
  padding-bottom: 20px;
  font-size: 20px;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: left;
}
.navList {
  display: flex;
  list-style: none;
  padding: 0;
  gap: 31px;
}
.navItem {
  color: var(--text-color-white);
  font-size: 16px;
  font-weight: 500;
  line-height: 21px;
  letter-spacing: 0.02em;
  text-align: left;
}
.footerCopyright {
  display: block;
  margin-left: -250px;
  color: var(--text-color-white);
  font-size: 16px;
  font-weight: 500;
  line-height: 21px;
  letter-spacing: 0.02em;
  text-align: left;
}
.svgBlock {
  display: flex;
  gap: 25px;
}
.socialLink {
  display: block;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  color: var(--text-color-white);
  transition: color 0.3s ease-in-out;
}
.socialLink:hover,
.socialLink:focus {
  color: var(--background-color-grey);
}
